// 

import React, { Fragment } from 'react';
import "./upDownLoad.scss";
import PropTypes from 'prop-types'; // 

import topIcon from "assets/img/public/top-icon.png";
import "assets/css/mescroll.min.css";
import MeScroll from "assets/js/mescroll.min.js";

class UpDownLoad extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mescroll: Object
    }
  }

  // 
  componentDidMount() {
    /* */
    const downCallback = () => {
      // ,;
      this.props.getAjaxData(undefined, this.state.mescroll); // 
      //,
      // mescroll.endErr();
    }

    /* page = {num:1, size:10}; num: 1, size: */
    const upCallback = (page) => {
      //,;
      this.props.getAjaxData(page, this.state.mescroll); // 
      //,
      // mescroll.endErr();
    }

    // MeScroll
    this.setState({
      mescroll: new MeScroll(this.props.id, {
        down: {
          auto: false, //callback; true
          callback: downCallback //
        },
        up: {
          lazyLoad: {
            use: true, // ,false
            attr: 'imgurl', //  (): <img imgurl='  src='''/>
            showClass: 'mescroll-lazy-in', // : ,mescroll.css
            delay: 500, // 500ms,
            offset: 200 // 200px,
          },
          auto: true, //; false
          isBounce: false, //ios,(,): http://www.mescroll.com/qa.html#q10
          callback: upCallback, //,;  callback: function (page) { upCallback(page); }
          page: {
            num: 0, // 0,1; callback(page)1
            size: 10 //,10
          },
          htmlNodata: '<p class="upwarp-nodata">--  --</p>',
          toTop: {
            //
            use: true,
						src: topIcon, //,null,
						offset: 1000 //1000px	
					},
        }
      })
    })
  }

  // 
  componentWillUnmount() {
    this.state.mescroll.destroy();
  }
  render() {
    return (<div id={ this.props.id } className={ this.props.className === undefined ? "mescroll" : "mescroll " + this.props.className}>
      {this.props.children}
    </div>)
  }
}

UpDownLoad.propTypes = {
  id: PropTypes.string.isRequired,
  getAjaxData: PropTypes.func.isRequired,
  className: PropTypes.string,
};

export default UpDownLoad;