// 

import React, { useState } from 'react';
import "./detailedParameter.scss";
import { Popup, Cell } from 'zarm';
import PopupBox from "common/popupBox/PopupBox"; // 

const DetailedParameter = (props) => {
  const [detailedParameterVisible, setDetailedParameterVisible] = useState(false);
  return(
    <div className="detailed-box">
      <div className="detailed-info"><span></span>500g</div>
      <div className="detailed-info"><span></span></div>
      <div className="detailed-info"><span></span>//</div>
      <div className="detailed-info"><span></span></div>
      <div className="detailed-info"><span></span>2020-07-01</div>
      <div className="detailed-info"><span></span>365</div>
      <div className="detailed-info" onClick={() => {setDetailedParameterVisible(true)}}><span>&gt;&gt;</span></div>
      {/*  */}
      <Popup
        visible={detailedParameterVisible}
        onMaskClick={() => {setDetailedParameterVisible(false)}}
        destroy={false}
      >
        <PopupBox title="" className="parameter-popup" closeBut={()=>{setDetailedParameterVisible(false)}}>
          <div className="parameter-box">
            <div className="parameter-unit"><span></span>500g</div>
            <div className="parameter-unit"><span></span></div>
            <div className="parameter-unit"><span></span>//</div>
            <div className="parameter-unit"><span></span></div>
            <div className="parameter-unit"><span></span>2020-07-01</div>
            <div className="parameter-unit"><span></span>365</div>
            <div className="parameter-unit"><span></span>500g</div>
            <div className="parameter-unit"><span></span></div>
            <div className="parameter-unit"><span></span>//</div>
            <div className="parameter-unit"><span></span></div>
            <div className="parameter-unit"><span></span>2020-07-01</div>
            <div className="parameter-unit"><span></span>365</div>
            <div className="parameter-unit"><span></span>500g</div>
            <div className="parameter-unit"><span></span></div>
            <div className="parameter-unit"><span></span>//</div>
            <div className="parameter-unit"><span></span></div>
            <div className="parameter-unit"><span></span>2020-07-01</div>
            <div className="parameter-unit"><span></span>365</div>
          </div>
        </PopupBox>
      </Popup>
    </div>
  )
}

export default DetailedParameter;