// 

import React, { Fragment, useState, useEffect } from 'react';
import "./storePromotion.scss";
import { Popup, Cell } from 'zarm';
import PopupBox from "common/popupBox/PopupBox"; // 
import ShopRecurrence from "common/shopRecurrence/ShopRecurrence"; // 
import ShopCoupons from "common/shopCoupons/ShopCoupons"; // 

const StorePromotion = (props) => {
  const [storePromotionVisible, setStorePromotionVisible] = useState(false); // 

  return (<Fragment>
    <Cell hasArrow className="shop-discount" onClick={() => {setStorePromotionVisible(true)}}>
      <div className="cashback"><b></b><span>292</span></div>
      <div className="coupon"><b></b><em>242</em><em>273</em><em>1477</em></div>
    </Cell>
    {/*  */}
    <Popup
      visible={storePromotionVisible}
      onMaskClick={() => {setStorePromotionVisible(false)}}
      destroy={false}
    >
      <PopupBox title="" className="store-promotion-popup" closeBut={()=>{setStorePromotionVisible(false)}}>
        <div className="store-promotion-box">
          <div className="title"></div>
          <ShopRecurrence dataJson={{value: 1, requirement: 24, speed: 3}} className="shop-recurrence-box" />
          <div className="title"></div>
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
          <ShopCoupons className="shop-coupons-box" />
        </div>
      </PopupBox>
    </Popup>
  </Fragment>)
  
}

export default StorePromotion;